import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const CoinTd = styled.td`
    border: 1px solid #cccccc;
    width: 40vh;
`;

const Button = styled.button`
    font-size: 1 rem;
    background-color: aqua;
    border-radius: 10px;
    cursor: pointer;
`;

export default function Coin(props) {

    const handleClick = (event) => {
        // prevent the default action of submitting the form
        event.preventDefault();
        props.handleRefresh(props.id);
    }

    const handleBuyTrade = (event) => {
        event.preventDefault();
        props.handleTrading(true, props.id);
    }

    const handleSellTrade = (event) => {
        event.preventDefault();
        props.handleTrading(false, props.id);
    }

    let balance = '*****';
    if (props.showBalance) {
        balance =
            <CoinTd>
                <span>{props.balance}</span>
            </CoinTd>
    }

    return (
        <>
        <tr>
            <CoinTd>{props.name}</CoinTd>
            <CoinTd>{props.ticker}</CoinTd>
            <CoinTd>${props.price}</CoinTd>
            {balance}
            <CoinTd>
                <form action="#" method="POST">
                    <Button className="btn btn-outline-primary" onClick={handleClick}>Refresh</Button>
                    <Button className="btn btn-outline-success" onClick={handleBuyTrade}>Buy</Button>
                    <Button className="btn btn-outline-danger" onClick={handleSellTrade}>Sell</Button>
                </form>
            </CoinTd>
        </tr>
        </>
    );
}

Coin.propTypes = {
    name: PropTypes.string.isRequired,
    ticker: PropTypes.string.isRequired,
    price: PropTypes.number.isRequired
}
